import style from './button.module.scss';
import { useFormik } from 'formik';

const Button = ({ text }) => {
  const { isSubmitting } = useFormik({});
  // isSubmitting    ,   , 
  //        ,      

  return (
    <button className={style.btn} type="submit" disabled={isSubmitting}>
      {text}
    </button>
  );
};

export default Button;
